import {
  Box,
  Button,
  IconButton,
  InputAdornment,
  Snackbar,
  TextField,
  Tooltip,
} from '@mui/material';
import React, { useState } from 'react';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import PictureAsPdfRoundedIcon from '@mui/icons-material/PictureAsPdfRounded';

function ShareOnLinkedIn({ certificate, selectedProg }) {
  const [copied, setCopied] = useState(false);

  const certificateUrl = `https://verify.dreamspace.academy/certificate/${selectedProg.id}/${certificate.id}`;

  const linkedInMessage = `ðŸš€ I am excited to share that I have completed the ${selectedProg.programmeName}!\n\nâœï¸ ${selectedProg.programmeDescription}\n\nðŸ“Œ Check out my certificate:\n${certificateUrl}`;

  const handleLinkedIn = () => {
    const linkedInShareUrl = `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(
      certificateUrl
    )}&text=${encodeURIComponent(linkedInMessage)}`;
    window.open(linkedInShareUrl, '_blank');
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(certificateUrl).then(() => {
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    });
  };

  return (
    <>
      <Box mt={2}>
        <Button
          variant="contained"
          fullWidth
          onClick={handleLinkedIn}
          startIcon={<LinkedInIcon />}
          sx={{ backgroundColor: '#1769aa' }}
        >
          Share on LinkedIn
        </Button>

        {certificate.certUrl && (
          <Button
            variant="contained"
            color="primary"
            onClick={() => window.open(certificate.certUrl, '_blank')}
            sx={{ mt: 2 }}
            startIcon={<PictureAsPdfRoundedIcon />}
            fullWidth
          >
            Download PDF
          </Button>
        )}

        <TextField
          fullWidth
          variant="outlined"
          value={certificateUrl}
          margin="normal"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <Tooltip title="Copy URL">
                  <IconButton
                    aria-label="copy link"
                    onClick={handleCopy}
                    edge="end"
                  >
                    <ContentCopyIcon />
                  </IconButton>
                </Tooltip>
              </InputAdornment>
            ),
          }}
        />
      </Box>
      <Snackbar
        open={copied}
        autoHideDuration={2000}
        message="URL copied to clipboard"
      />
    </>
  );
}

export default ShareOnLinkedIn;
