import React, { useEffect, useState } from 'react';
import {
  Box,
  Card,
  CardContent,
  Container,
  Grid,
  Typography,
  CircularProgress,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { useParams } from 'react-router-dom';
import useGoogleSheetData from '../hooks/useGoogleSheetData';
import { csvUrl } from '../utils/fileUrl';
import Papa from 'papaparse';
import HomeAppBar from '../components/HomeAppBar';
import ShareOnLinkedIn from '../components/view-certificate/ShareOnLinkedIn';
import { Helmet } from 'react-helmet';
import ShareQRCode from '../components/ShareQRCode';
import VerifiedBadge from '../assets/verified-badge.png';
import CertificateInfoCard from '../components/view-certificate/CertificateInfoCard';

export default function CertificatePage() {
  const { programId, certificateId } = useParams();
  const [selectedProg, setSelectedProg] = useState(null);
  const [certificate, setCertificate] = useState(null);
  const [loading, setLoading] = useState(true);
  const { sheetData: programs, loading: loadingPrograms } =
    useGoogleSheetData(csvUrl);
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    if (!loadingPrograms) {
      const selectedProgram = programs.find((prog) => prog.id === programId);
      if (selectedProgram) {
        setSelectedProg(selectedProgram);
      }
    }
  }, [loadingPrograms, programs, programId]);

  useEffect(() => {
    if (selectedProg) {
      const certificateUrl = csvUrl.replace('gid=0', `gid=${selectedProg.gId}`);
      Papa.parse(certificateUrl, {
        download: true,
        header: true,
        complete: (result) => {
          const certificateData = result.data.find(
            (cert) => cert.id === certificateId
          );
          setCertificate(certificateData);
          setLoading(false);
        },
      });
    }
  }, [selectedProg, certificateId]);

  const certificateUrl = `https://verify.dreamspace.academy/certificate/${programId}/${certificateId}`;

  if (loading || loadingPrograms) {
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        height="100vh"
      >
        <CircularProgress />
      </Box>
    );
  }

  if (!certificate) {
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        height="100vh"
      >
        <Typography variant="h6">Certificate not found</Typography>
      </Box>
    );
  }

  return (
    <>
      <Helmet>
        <title>{`Certificate of ${certificate.name}`}</title>
        <meta
          property="og:title"
          content={`Certificate of ${certificate.name}`}
        />
        <meta
          property="og:description"
          content={`I have successfully completed the ${selectedProg.initiative}. Check out my certificate!`}
        />
        <meta
          property="og:image"
          content="https://verify.dreamspace.academy/dss-banner.jpg"
        />
        <meta property="og:url" content={certificateUrl} />
        <meta property="og:type" content="website" />
      </Helmet>
      <HomeAppBar />
      <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Card>
              <CardContent
                style={{
                  paddingLeft: isSmallScreen && 0,
                  paddingRight: isSmallScreen && 0,
                  paddingBottom: isSmallScreen && 0,
                }}
              >
                <>
                  <Box
                    display="flex"
                    justifyContent="space-between"
                    alignItems="center"
                    mb={2}
                  >
                    <Typography variant="h5" marginLeft={2}>
                      {certificate.name}'s Certificate
                    </Typography>
                  </Box>
                  <Box
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      alignContent: 'center',
                    }}
                    mb={3}
                  >
                    <img
                      src={VerifiedBadge}
                      alt="Verified by DreamSpace Academy"
                      style={{ width: isSmallScreen ? '100%' : '30%' }}
                    />
                  </Box>
                  <CertificateInfoCard
                    selectedProg={selectedProg}
                    certificate={certificate}
                  />
                </>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} md={4}>
            <ShareOnLinkedIn
              certificate={certificate}
              selectedProg={selectedProg}
            />

            <ShareQRCode certificateUrl={certificateUrl} />
          </Grid>
        </Grid>
      </Container>
    </>
  );
}
