import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  Box,
  Button,
  useMediaQuery,
} from '@mui/material';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import SyncIcon from '@mui/icons-material/Sync';
import PersonIcon from '@mui/icons-material/Person';
import EventIcon from '@mui/icons-material/Event';
import SchoolIcon from '@mui/icons-material/School';
import { useTheme } from '@mui/material/styles';

const CertificateCard = ({ certificate, selectedProg }) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <>
      <Card
        sx={{
          backgroundColor: '#f9f1ff',
          borderRadius: '12px',
          padding: 2,
          width: '90%',
        }}
      >
        <CardContent
          style={{
            paddingLeft: isSmallScreen && 0,
            paddingRight: isSmallScreen && 0,
            paddingBottom: isSmallScreen && 0,
          }}
        >
          <Typography variant="body2" mb={2}>
            {selectedProg.programmeDescription}
          </Typography>
          {/** Certificate Details */}
          {[
            {
              icon: <PersonIcon />,
              label: 'Recipient',
              value: certificate.name,
            },
            {
              icon: <EventIcon />,
              label: 'Issued Date',
              value: certificate.issuedOn,
            },
            {
              icon: <SchoolIcon />,
              label: 'Program',
              value: selectedProg.programmeName,
            },
            { icon: <SyncIcon />, label: 'Certificate Status', value: 'Valid' },
            {
              icon: <AccountBalanceIcon />,
              label: 'Certificate Issuer',
              value: selectedProg.initiative,
              logo: selectedProg.avatar,
            },
          ].map((item, index) => (
            <Box
              key={index}
              display="flex"
              flexDirection={isSmallScreen ? 'column' : 'row'}
              alignItems={isSmallScreen ? 'flex-start' : 'center'}
              justifyContent="space-between"
              mb={1}
              p={1}
              borderRadius="8px"
              sx={{ backgroundColor: '#38025f', color: 'white' }}
            >
              <Box
                display="flex"
                alignItems="center"
                mb={isSmallScreen ? 1 : 0}
              >
                {React.cloneElement(item.icon, {
                  sx: { color: 'white', marginRight: 1 },
                })}
                <Typography>{item.label}</Typography>
              </Box>
              {['Valid', 'Verified'].includes(item.value) ? (
                <Button
                  variant="contained"
                  size="small"
                  sx={{
                    backgroundColor: 'white',
                    color: 'black',
                    '&:hover': { backgroundColor: '#ddd' },
                  }}
                >
                  {item.value}
                </Button>
              ) : (
                <>
                  {item.label === 'Certificate Issuer' ? (
                    <Box display="flex" alignItems="center">
                      <Box
                        component="img"
                        src={item.logo} // Program logo
                        alt={`${item.value} Logo`}
                        sx={{
                          width: 35,
                          height: 35,
                          borderRadius: '50%',
                          mr: 2,
                        }}
                      />
                      <Typography sx={{ fontWeight: 'bold' }}>
                        {item.value}
                      </Typography>
                    </Box>
                  ) : (
                    <Typography sx={{ fontWeight: 'bold' }}>
                      {item.value}
                    </Typography>
                  )}
                </>
              )}
            </Box>
          ))}
        </CardContent>
      </Card>
    </>
  );
};

export default CertificateCard;
